.home {
  padding: 24px;
  background: #fff;
  min-height: 850px;
}
.home .home-card {
  float: left;
}
.home .home-content {
  position: absolute;
  top: 420px;
  width: 76%;
  border: 1px solid #e8e8e8;
}
.home .home-content .ant-card-body {
  display: flex;
  flex-wrap: wrap;
  padding: 24px;
  zoom: 1;
  justify-content: space-around;
}
.home .home-content .home-menu {
  font-size: 20px;
}
.home .home-content .home-menu span {
  cursor: pointer;
}
.home .home-content .home-menu .home-menu-active {
  border-bottom: 2px solid #1DA57A;
  color: #1DA57A;
  padding: 0 0 16px 0;
}
.home .home-content .home-menu .home-menu-visited {
  margin-right: 40px;
}
.home .home-content .home-table-left {
  margin-bottom: 20PX;
  width: 600px;
}
.home .home-content .home-table-right {
  width: 330px;
}
